<template>
  <div class="cartoonContent">
    <div class="xxx" v-for="(cartoonImg, index) in cartoonImgs" :key="index">
      <img :src="cartoonImg.img" alt />
    </div>
    <div class="btn" v-if="cartoonImgs">
      <button @click="lastWords">上一话</button>
      <button @click="nextWords">下一话</button>
      <van-icon name="arrow-left" @click="$router.go(-1)" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cartoonImgs: []
    };
  },
  methods: {
    lastWords() {
      if (this.$store.state.curentindex >= 0) {
        this.getCartoonContents(
          this.$store.state.cartoonLists[this.$store.state.curentindex--].url
        );
      }
    },
    nextWords() {
      // window.console.log(this.$store.state.cartoonLists[this.$store.state.curentindex++].url)
      this.getCartoonContents(
        // window.console.log(this.$store.state.curentindex++),
        this.$store.state.cartoonLists[this.$store.state.curentindex++].url
      );
    },
    getCartoonContents(url) {
      this.axios
        .get("https://api.kele8.cn/agent/http://api.pingcc.cn?mhurl2=" + url)
        .then(response => {
          this.cartoonImgs = response.data.list;
        });
    },
    getCartoonContent(url) {
      this.axios
        .get("https://api.kele8.cn/agent/http://api.pingcc.cn", {
          params: {
            mhurl2: url
          }
        })
        .then(response => {
          this.cartoonImgs = response.data.list;
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getCartoonContent(to.query.id);
    });
  }
};
</script>

<style lang="less">
.cartoonContent {
  font-size: 0;
  .van-icon {
    position: fixed;
    bottom: 0;
    right: 0;
    width: 18px;
    height: 18px;
    color: pink;
    font-size: 18px;
  }
  .btn {
    font-size: 16px;
    position: fixed;
    bottom: 0;
    left: 0;
  }
}
</style>