import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import HomePage from "../views/HomePage.vue";
import HomeNew from "../views/HomeNew.vue";
import HomeClassify from "../views/HomeClassify.vue";
import HomeBookrack  from "../views/HomeBookrack.vue";
import Search from '../views/Search.vue'
import CartoonIntroduced from '../views/CartoonIntroduced.vue'
import CartoonContent from '../views/CartoonContent.vue'
import Ranking from '../views/Ranking.vue'

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        component: Home,
        redirect: '/HomePage' ,
        children:[
          {
            path: "HomePage",
            component: HomePage,
          },
          {
            path:"HomeNew",
            component:HomeNew
          },
          {
            path:"HomeClassify",
            component:HomeClassify
          },
          {
            path:"HomeBookrack",
            component:HomeBookrack 
          },
        ]
    },
    {
      path:"/search",
      component:Search
    },
    {
      path:"/cartoonIntroduced",
      component:CartoonIntroduced,
      // keepAlive:true,//缓存 
    },
    {
      path:'/cartoonContent',
      component:CartoonContent
    },
    {
      path:'/ranking',
      component:Ranking
    }
];

const router = new VueRouter({
    routes
});

export default router;
