<template>
  <div class="page">
    <div class="nav">
      <div class="left">caricature</div>
    </div>
    <van-swipe :autoplay="3000" indicator-color="white" v-if="carousel[0]">
      <van-swipe-item v-for="(item, index) in carousel" :key="index">
        <img :src="item.carouselUrl" alt @click="getTrace(item.carouselName)" />
      </van-swipe-item>
    </van-swipe>
    <ul class="btn-wrap">
      <li @click="jumpNew">
        <img src="../assets/e2196490-8c2e-11e9-853c-67ef947739d8.png" alt />
        <p>最新</p>
      </li>
      <li @click="jumpRanking">
        <img src="../assets/edf15070-8c2e-11e9-8f6e-9342fa54c082.png" alt />
        <p>排行</p>
      </li>
      <li @click="jumpClassify">
        <img src="../assets/facf1390-8c2e-11e9-bab0-7d9ad31828da.png" alt />
        <p>分类</p>
      </li>
    </ul>
    <Title>漫画连载中</Title>
    <ul class="cart">
      <Cartoon-cards
        v-for="(item, index) in plus"
        :key="index"
        :index="index"
        :cartoon-cards="item"
      ></Cartoon-cards>
    </ul>
  </div>
</template>

<script>
import Title from "@/components/Title.vue";
import CartoonCards from "@/components/CartoonCards.vue";
export default {
  data() {
    return {
      carousel: [
        {
          carouselName: "国民老公的小仓鼠",
          carouselUrl: "http://image.mhxk.com/mh/107850_2_1.jpg-noresize.webp"
        },
        {
          carouselName: "风起苍岚",
          carouselUrl: "http://image.mhxk.com/mh/9680_2_1.jpg-noresize.webp"
        },
        {
          carouselName: "我家大师兄脑子有坑",
          carouselUrl: "http://image.mhxk.com/mh/27404_2_1.jpg-noresize.webp"
        },
        {
          carouselName: "长安幻想",
          carouselUrl: "http://image.mhxk.com/mh/107527_2_1.jpg-noresize.webp"
        }
      ],
      plus: [],
      kkk: []
    };
  },
  components: {
    Title,
    CartoonCards
  },
  methods: {
    getTrace(x) {
      window.console.log(x);
      this.axios
        .get("http://api.pingcc.cn", {
          params: {
            mhname: x
          }
        })
        .then(response => {
          this.kkk = response.data.list[0].url;
          window.console.log(response.data);
          this.jump(this.kkk);
        });
    },
    jump(param) {
      this.$router
        .push({
          path: "/cartoonIntroduced",
          query: {
            id: param
          }
        })
        .catch(err => {
          err;
        });
    },
    getPlus() {
      let plus = JSON.parse(window.localStorage.getItem("plus"));
      if (plus && Date.now() < plus.time) {
        this.plus = plus.data;
      } else {
        this.axios.get("http://api.pingcc.cn/?mhlb=wanjie").then(response => {
          ({ list: this.plus } = response.data);
          window.console.log(response);
          this.plus.shift();
          this.plus.pop();
          this.plus.pop();
          //本地存储
          window.localStorage.setItem(
            "plus",
            JSON.stringify({
              time: Date.now() + 120 * 60 * 1000,
              data: this.plus
            })
          );
        });
      }
    },
    getCurrentSearchKeyword() {
      this.$router.push({
        path: "/search"
      });
    },
    jumpNew() {
      this.$router.push({
        path: "/HomeNew"
      });
    },
    jumpRanking() {
      this.$router.push({
        path: "/ranking"
      });
    },
    jumpClassify() {
      this.$router.push({
        path: "/HomeClassify"
      });
    }
  },
  mounted() {},
  created() {
    this.getPlus();
  }
};
</script>

<style lang="less">
.page {
  position: relative;
  .nav {
    width: 100%;
    height: 7vh;
    position: fixed;
    top: 0;
    left: 0;
    background: #eee;
    padding: 0 10px;
    .left {
      font-size: 32px;
      font-family: "Srisakdi", cursive;
      line-height: 42px;
      // font-weight: 600;
      // color: #35972c;
    }
  }
  .van-swipe {
    width: 100%;
    height: 28vh;
    margin-top: 7vh;
    img {
      width: 100%;
    }
  }
  .btn-wrap {
    display: flex;
    li {
      flex: 1;
      text-align: center;
      transform: scale(0.6);
      // line-height: 45px;
    }
  }
  ul.cart {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
}
</style>