<template>
  <li class="cartoonList" @click="comicPage(cartoonCards.url)">
    <img :src="cartoonAmendment()" alt />
    <p class="word">{{this.cartoonCards.name}}</p>
  </li>
</template>

<script>
export default {
  props: ["cartoonCards"],
  methods: {
    cartoonAmendment() {
      return this.cartoonCards.cover;
    },
    comicPage(param) {
      this.$router.push({
        path: "/cartoonIntroduced",
        query: {
          id: param
        }
      });
    }
  }
};
</script>

<style lang="less">
li.cartoonList {
  margin-top: 12px;
  width: 30%;
  height: 25%;
  display: inline-block;
  flex: 1 0 30%;
  &:nth-child(3n + 2) {
    margin: 12px 5px;
  }
  img {
    width: 100%;
    height: 138px;
    vertical-align: middle;
  }
  .word {
    font-size: 12px;
    text-align: center;
    color: #888;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
</style>