<template>
  <div class="search">
    <van-icon name="arrow-left" @click="$router.go(-1)" />
    <label for>
      <input class="inp" type="text" v-model="inputValue" />
      <input type="submit" />
    </label>
    <template v-if="!inputValue">
      <div class="hotSearch">
        <p>热门搜索</p>
        <ul class="hotlist">
          <li
            v-for="(hotSearch, index) in hotSearchs"
            :key="index"
            @click="check(hotSearch.comic_name)"
          >{{hotSearch.comic_name}}</li>
        </ul>
      </div>
    </template>
    <template v-else>
      <ul class="searchList">
        <li
          v-for="(searchSuggest, index) in searchSuggests"
          :key="index"
          @click="comicPage(searchSuggest.url)"
        >{{searchSuggest.name}}</li>
      </ul>
      <!-- <div>{{inputValue}}</div> -->
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inputValue: null,
      hotSearchs: [],
      searchSuggests: [],
      thun: ""
    };
  },
  methods: {
    check(i) {
      this.axios
        .get("https://api.kele8.cn/agent/http://api.pingcc.cn", {
          params: {
            mhname: i
          }
        })
        .then(response => {
          this.thun = response.data.list[0].url;
          this.nia(this.thun);
          window.console.log(response.data)
        });
    },
    nia(param) {
      // window.console.log(param)
      this.$router.push({
        path: "/cartoonIntroduced",
        query: {
          id: param
        }
      });
    },
    getHotSearch() {
      this.axios
        .get(
          "https://api.kele8.cn/agent/https://m.manhuatai.com/api/gettopsearch"
        )
        .then(response => {
          this.hotSearchs = response.data.data;
          window.console.log(this.hotSearchs);
        });
    },
    getSearchSuggest() {
      this.axios
        .get("https://api.kele8.cn/agent/http://api.pingcc.cn", {
          params: {
            mhname: this.inputValue
          }
        })
        .then(response => {
          this.searchSuggests = response.data.list;
          this.searchSuggests.forEach((element, index) => {
            var str = element.name;
            var n = str.indexOf(this.inputValue);
            if (n == -1) {
              this.searchSuggests.splice(index, 1);
              window.console.log(element.name);
            }
            window.console.log(n);
          });
          window.console.log(this.searchSuggests);
        });
    },
    comicPage(param) {
      this.$router.push({
        path: "/cartoonIntroduced",
        query: {
          id: param
        }
      });
    }
  },
  created() {
    this.getHotSearch();
  },
  watch: {
    inputValue(newValue) {
      if (newValue) {
        this.getSearchSuggest();
      }
    }
  }
};
</script>

<style lang="less">
.search {
  padding: 40px 12px;
  label {
    .inp {
      width: 80%;
      line-height: 24px;
      padding: 0 5px;
      font-size: 12px;
    }
    input[type="submit"] {
      border: 1px solid #888;
      border-left: none;
      background-color: skyblue;
      line-height: 20px;
      padding: 3px 5px;
      text-align: center;
      font-size: 12px;
    }
  }
  .hotSearch {
    p {
    }
    ul.hotlist {
      display: flex;
      flex-wrap: wrap;
      li {
        padding: 0 10px;
        margin: 2px 3px 3px 0;
        border: 1px solid #888;
        border-radius: 10px;
      }
    }
  }
}
</style>