<template>
  <div class="introduced">
    <template v-if="cartoonDetails">
      <van-icon name="arrow-left" @click="$router.go(-1)" />
      <div class="top">
        <div class="bg" :style="{'background-image':`url(${cartoonDetails.cover})`}"></div>
        <div class="photo">
          <img :src="cartoonDetails.cover" alt />
        </div>
        <h1 class="name">{{this.cartoonDetails.name}}</h1>
        <p class="author">{{this.cartoonDetails.author}}</p>
      </div>
      <div class="btn">
        <button class="startRead">开始阅读</button>
      </div>
      <van-tabs v-model="active">
        <van-tab title="目录">
          <p
            v-for="(cartoonList, index) in  cartoonLists"
            :key="index"
            @click="cartoonContent(cartoonList.url,index)"
          >{{cartoonList.num}}</p>
        </van-tab>
        <van-tab title="详情" v-if="cartoonDetails">
          <p>{{cartoonDetails.introduce}}</p>
        </van-tab>
      </van-tabs>
    </template>
    <template v-else>
      <van-icon name="arrow-left" @click="$router.go(-1)" />
      <div>我还在搜索，可能需要好久</div>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cartoonDetails: null,
      cartoonLists: [],
      active: 2
    };
  },
  methods: {
    getCartoonIntroduced(url) {
      this.axios
        .get("http://api.pingcc.cn/", {
          params: {
            mhurl1: url
          }
        })
        .then(response => {
          this.cartoonDetails = response.data.data;
          this.cartoonLists = response.data.list;
          this.$store.commit("submitcartoonLists", response.data.list);
          window.console.log(response);
        })
    },
    cartoonContent(param, i) {
      this.$store.commit("submitcurentindex", i);
      // window.console.log(i);
      this.$router.push({
        path: "/cartoonContent",
        query: {
          id: param
        }
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    
    next(vm => {
      vm.getCartoonIntroduced(to.query.id);
      window.console.log(to.query.id)
      window.scrollY = 0
    });
  }
};
</script>

<style lang="less">
.introduced {
  .van-icon {
    font-size: 30px;
  }
  .top {
    position: relative;
    .bg {
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: 50%;
      transform: scale(1.5);
      filter: blur(20px);
      z-index: -1;
      position: absolute;
      top: 0;
      left: 0;
    }
    .photo {
      width: 120px;
      background: skyblue;
      img {
        width: 100%;
        vertical-align: bottom;
      }
    }
    h1.name {
      text-align: center;
      font-size: 28px;
      font-weight: bolder;
    }
    p.author {
      text-align: center;
      margin-bottom: 20px;
    }
  }
  .btn {
    text-align: center;
  }
}
</style>