<template>
  <div class="new">
    <Title>最新漫画</Title>
    <ul class="father" style="padding:6px 8px">
      <li class="son" v-for="(item, index) in news" :key="index" @click="comicPage(item.url)">
        <div class="img">
          <img :src="item.cover" alt />
        </div>
        <div class="info">
          <p>名称：{{item.name}}</p>
          <p>更新日期：{{item.time}}</p>
          <p>最新话：{{item.latest}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Title from "@/components/Title.vue";
export default {
  data() {
    return {
      news: []
    };
  },
  components: {
    Title
  },
  methods: {
    getNew() {
      this.axios
        .get("http://api.pingcc.cn/?mhlb=new")
        .then(response => {
          this.news = response.data.list;
          this.news.shift();
          window.console.log(this.news);
        });
    },
    comicPage(param) {
      this.$router.push({
        path: "/cartoonIntroduced",
        query: {
          id: param
        }
      });
    }
  },
  created() {
    this.getNew();
  }
};
</script>

<style lang="less">
ul.father {
  padding: 0 10px;
  li.son {
    display: flex;
    margin-bottom: 10px;
    background: #eee;
    .img {
      flex: 1;
      img {
        width: 100px;
      }
    }
    .info {
      font-size: 14px;
      color: #999;
      margin-left: 5%;
      margin-top: 3%;
      flex: 2;
      p {
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
    }
  }
}
</style>