import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        cartoonLists: null,
        curentindex: null
    },
    mutations: {
        submitcartoonLists(state, i) {
            state.cartoonLists = i;
        },
        submitcurentindex(state,i){
          state.curentindex=i
        }
    },
    actions: {},
    modules: {}
});
